<template>
  <div class="loadingdata" :class="{'loaderror': show, 'loadsuccess': !show}">
    <!-- <h2 style="margin: 0" v-if="show">{{msg}}</h2> -->
    <h2 style="margin: 0" v-if="show">参数加载失败，请检查网络状态和服务状态...</h2>
  </div>
</template>

<script>
  export default {
    title: '加载参数',
    data () {
      return {
        msg: '',
        show: false
      }
    },
    ready () {
      this.$appdata.load().then(() => {
        this.$emit('ready')
      })
    }
  }
</script>
<style media="screen">
  .loadingdata {
    position: fixed;
    background: #fff;
    opacity: 0.6;
    top: 0;
    width: 100%;
  }
  .loaderror {
    opacity: 1;
  }
  .loadsuccess {
    display: none;
  }
</style>



